.menu {
  display: flex;
  margin: 30px 0 0 0;
  top: -30px;
  position: absolute;
  align-self: center;
  /* left: 50%;
  right: 50%; */
  /* overflow: hidden; */
  width: 100%;
}
.menu.sticky {
  position: fixed;
  background-color: rgb(9, 8, 24);
  opacity: 0.9;
  /* overflow: auto; */
  transition: 1s;
  z-index: 10;
}
  .hamburger {
    display: none;
  }
  .menu--dropdown {
    /* justify-content: space-evenly; */
    justify-content: flex-start;
    display: grid;
    grid-template-columns: repeat(6, auto);
    /* position: sticky;
    position: -webkit-sticky; */
    top: -1px;
    /* background-color: #fff; */
    z-index: 9999;
    transition: height 2s;
    width: 100%;
  }

@media only screen and (max-width: 420px) {
.menu {
  margin: 10px 15px 0 10px;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  
}
  .hamburger {
    display: flex;
    justify-content: flex-end;
    margin: 25px 32px 0 0;
    cursor: pointer;
    color: black;
  }
  
    .menu--dropdown {
      grid-template-columns: 100%;
      grid-template-rows: repeat(5, auto);
      padding: 20px;
      transition: height 2s;

    }

    .menu--dropdown.open {
      height: 100%;
      width: 120px;
      padding: 0;
      padding-top: 10px;
      visibility: visible;
      display: block;
      background-color: #f9f9f9;
      color: black;
      z-index: 1;
      transition-timing-function: linear;
    }
    .menu--dropdown.closed {
      /* height: 0px; */
      display: none;  
      transition-timing-function: linear;
    }

  
}
 

  